<script>
import axios from "axios";
import myCaptcha from "vue-captcha";
import VueRecaptcha from "vue-recaptcha";
import modalAdded from '@/components/commons/CartAdded';
import helper from '@/script/helper';

var globallanguage = "es";
if (globaldata.language != null && globaldata.language != "") {
  globallanguage = globaldata.language;
}
var globallocalizador = globaldata.localizador;

var iswhitebrand = false;

/* if (globaldata.whitebrandgeneric !== null && globaldata.whitebrandgeneric !== '') {
    iswhitebrand = true
  } */
const langfile = require("../../assets/lang/content.json");

export default {
  name: "Detalle",
  data: function() {
    return {
      detalles: null,
      showwhite: iswhitebrand,
      key: {
        ratekey: null,
        datestart: null,
        dateend: null,
        adultos: null,
        ninos: null,
        units: null,
        variantesel: null,
        variantename: null,
        name: null
      },
      titular: null,
      comentarios: null,
      telefono: null,
      email: null,
      loader: null,
      selectedSuppl: null,
      coupon: null,
      language: globaldata.language,
      multilanguage: langfile.multilanguage,
      reseolvedCaptcha: false,
      recaptchatoken: globaldata.recaptchatoken,
      captchaValidation: globaldata.captchaValidation,
      nocart: globaldata.nocart,
      termsaccepted: false,
      unmailing:false,
      fechasalidaformat: null,
      fechaentradaformat: null,
      nobreakdown: globaldata.nobreakdown
    };
  },

  components: {
    "my-captcha": myCaptcha,
    VueRecaptcha,
    'modalAdded': modalAdded
  },
  methods: {
    verify: function() {
      // this.status = "submitting";
      this.$refs.recaptcha.execute();
    },
    onCaptchaVerified: function(recaptchaToken) {
      const _this = this;
      _this.status = "submitting";
      _this.$refs.recaptcha.reset();
      _this.book(recaptchaToken);
    },
    onCaptchaExpired: function() {
      this.$refs.recaptcha.reset();
    },
    captchaOk() {
      this.$data.reseolvedCaptcha = true;
    },
     formatDatetoview(dtf) {
      if (dtf) {
        let dateArray = String(dtf).split("-");
        let fecha = "";
        console.log(dtf);
        switch (globaldata.language) {
          case "es":
            fecha = dateArray[2] + "-" + dateArray[1] + "-" + dateArray[0];
          case "en":
            fecha = dateArray[1] + "-" + dateArray[2] + "-" + dateArray[0];
          default:
            fecha = dateArray[2] + "-" + dateArray[1] + "-" + dateArray[0];
        }
        return fecha;
      }
    },
    goback(page) {
      switch (page) {
        case "formulario":
          this.$router.push({ name: "main" });
          break;
        case "dispo":
          this.$router.push({
            name: "otros_dispo",
            query: {
              destino: this.$route.query.destino
            }
          });
          break;
        case "rates":
          this.$router.push({
            name: "otros_rates",
            query: {
              destino: this.$route.query.destino,
              genericname: this.$route.query.genericname,
              key: this.$route.query.key,
              entrada: this.$route.query.entrada,
              salida: this.$route.query.salida,
              adults: this.$route.query.adults,
              children: this.$route.query.children,
              variantesel: this.$route.query.variantesel,
              units: this.$route.query.units
            }
          });
          break;
        default:
      }
    },
    procesar: function(response) {
      if (response.data.statusCode === 200)
        this.$router.push({
          name: "ok",
          query: {
            bookingId: response.data.bookingId,
            availableservices: response.data.availableServices,
            paymenturl: response.data.paymentUrl
          }
        });
      else this.$router.push({ name: "ko", query: { msg: response.data.msg } });
    },
    //cartversion-i
    addtocart: function() {
      var d = this.$data;
      var reference = this.gettimereference();
      if (this.checkForm()) {
        var cartline = {
          bookingtype: "otros",

          leadName: d.titular,
          commentsToProvider: d.comentarios,
          privateComments: "",
          email: d.email,
          phoneNumber: d.telefono,

          rateKeys: d.key,
          itemDescription: this.mountdescription(),

          total: d.detalles.total,
          paymentLines: d.detalles.paymentLines,
          cancellationCosts: d.detalles.cancellationCosts,
           cancellationFreeDate: d.detalles.cancellationFreeDate,
          prices: d.detalles.priceLines,
          remarks: d.detalles.remarks,
          services: [],
          bookingReference: reference
        };

        var currentCart = localStorage.qtse_cart
          ? JSON.parse(localStorage.getItem("qtse_cart"))
          : [];
        currentCart.push(cartline);
        localStorage.setItem("qtse_cart", JSON.stringify(currentCart));

           this.storageData();

        this.$refs.modalAdded.showModal()
      }
    },
    mountdescription: function() {
      var _this = this;

      var desc = ' <div class="title"></b>' + this.$data.key.name + "</div>";
      if (this.$data.key.datestart || this.$data.key.dateend) {
        let line1 =
          '<div class="line1"> <span><b style="margin-right:0.5em;">' +
          _this.multilanguage.date[this.$data.language] +
          ": </b> " +
          this.$data.key.datestart;

        if (this.$data.key.dateend) {
          line1 = line1 + " - " + this.$data.key.dateend + "</span></div>";
        } else {
          line1 = line1 + "</span></div>";
        }
        desc = desc + line1;
      }

      if (this.$data.key.adults && this.$data.key.adults > 0) {
        let line2 =
          '<div class="line2"> <span><b style="margin-right:0.5em;">' +
          _this.multilanguage.pax[this.$data.language] +
          ": </b> " +
          this.$data.key.adults +
          " " +
          _this.multilanguage.adults[this.$data.language];

        if (this.$data.key.children) {
          line2 =
            line2 +
            " " +
            this.$data.key.children +
            "  " +
            _this.multilanguage.adults[this.$data.language];
        }
        line2 = line2 + "</span></div>";

        desc = desc + line2;
      }
      if (this.$data.key.units && this.$data.key.units > 0) {
        let line3 =
          '<div class="line2"> <span><b style="margin-right:0.5em;">' +
          _this.multilanguage.units[this.$data.language] +
          ": </b> " +
          this.$data.key.units +
          "</span></div>";
        desc = desc + line3;
      }
      if (this.$data.key.variantename) {
        let line4 =
          '<div class="line3"> <span><b style="margin-right:0.5em;">' +
          _this.multilanguage.variant[this.$data.language] +
          ": </b> " +
          this.$data.key.variantename +
          "</span></div>";
        desc = desc + line4;
      }

      return desc;
    },
 storageData: function() {
      let maindata = {
        leadName: this.$data.titular,
        contactPhone: this.$data.telefono,
        email: this.$data.email,
        startdate: this.$route.query.entrada,
        enddate: this.$route.query.salida
      };

      localStorage.setItem("qtse_maindatastorage", JSON.stringify(maindata));
    },getStorageData: function() {
      let maindata = JSON.parse(localStorage.getItem("qtse_maindatastorage"));
      if(maindata !== null){
        if (maindata.leadName) {
          this.$data.titular = maindata.leadName;
        }
        if (maindata.contactPhone) {
          this.$data.telefono = maindata.contactPhone;
        }
        if (maindata.email) {
          this.$data.email = maindata.email;
        }
      }
    },

    //cartversion-f
    book: function(recaptchaToken) {
      var esto = this;
      var d = this.$data;
      var reference = this.gettimereference();
      if (this.checkForm()) {
        d.loader = true;
        axios({
          method: "put",
          url: globaldata.baseurl + "/generic/booking",
          data: {
            language: d.language,
            key: this.$route.query.ratekey,
            captchaToken: recaptchaToken,
            supplementIds: d.key.suplsel,
            bookingReference: reference,
            leadName: this.$data.titular,
            commentsToProvider: this.$data.comentarios,
            phoneNumber: this.$data.telefono,
            email: this.$data.email,
            mailingUnwanted: d.unmailing
          }
        })
          .then(function(response) {
            d.loader = false;
            esto.procesar(response);
          })
          .catch(function(error) {
            d.loader = false;
            console.log(error);
            esto.$router.push({ name: "ko", query: { msg: error } });
          });
      }
    },
    gettimereference: function() {
      var d = new Date();
      var month = "" + (d.getMonth() + 1);
      var day = "" + d.getDate();
      var year = d.getFullYear();
      var hour = d.getHours().toString();
      var minute = d.getMinutes().toString();
      var seconds = d.getSeconds().toString();

      if (month.length < 2) month = "0" + month;
      if (day.length < 2) day = "0" + day;
      if (hour.length < 2) hour = "0" + hour;
      if (minute.length < 2) minute = "0" + minute;
      console.log(seconds.length);
      if (seconds.length < 2) seconds = "0" + seconds;
      console.log(seconds);
      return (
        year.toString().substr(2, 2) +
        month.toString() +
        day.toString() +
        hour.toString() +
        minute.toString() +
        seconds.toString()
      );
    },
    getDatafromLocator: function() {
      var d = this.$data;
      var _this = this;

      axios({
        method: "get",
        url:
          globaldata.baseurl + "/commons/fromlocator/" + globaldata.localizador,
        params: {}
      })
        .then(function(response) {
          setTimeout(function() {
            d.titular = response.data.leadName;
            d.email = response.data.email;
          });
        })
        .catch(function(error) {
          d.loader = null;
          console.log(error);
        });
    },
    checkForm: function(e) {
      var existsErrors = false;
      this.cleanErrors();
      if (this.$data.detalles.terms && this.$data.nocart) {
        if (this.$data.termsaccepted !== true) {
          this.setValidationError(
            "termandcondcheck",
            "termandcondcheck-msg",
            this.multilanguage.pleaseacceptterms[this.language]
          );
          this.setValidationError(
            "termandcondcheck2",
            "termandcondcheck2-msg",
            this.multilanguage.pleaseacceptterms[this.language]
          );
          existsErrors = true;
        }
      }
      if (this.$data.email === null || this.$data.email === "") {
        this.setValidationError(
          "email",
          "email-msg",
          this.multilanguage.erroremailempty[this.language]
        );
        existsErrors = true;
      } else {
        if (!this.validateEmail(this.$data.email)) {
          this.setValidationError(
            "email",
            "email-msg",
            this.multilanguage.erroremailnotvalid[this.language]
          );
          existsErrors = true;
        }
      }
      if (this.$data.titular === null || this.$data.titular === "") {
        this.setValidationError(
          "name",
          "name-msg",
          this.multilanguage.errorleadnameempty[this.language]
        );
        existsErrors = true;
      }
      if (existsErrors) {
        return false;
      }
      return true;
    },
    setValidationError(field, msgField, msg) {
      var element = document.getElementById(field);

      element.classList.add("is-invalid");
      // element.classList.add('form-control')
      var att = document.createAttribute("aria-invalid");
      att.value = "true";
      element.setAttributeNode(att);
      element.focus();
      if (msgField !== null && msgField !== " ") {
        var elementmsg = document.getElementById(msgField);

        elementmsg.innerHTML = msg;
      }
    },
    cleanErrors: function(email) {
      var element = document.getElementById("name");
      if (element) {
        element.setAttribute("aria-invalid", "false");
        element.setAttribute("class", "form-control");
      }

      element = document.getElementById("email");
      if (element) {
        element.setAttribute("aria-invalid", "false");
        element.setAttribute("class", "form-control");
      }
      element = document.getElementById("termandcondcheck");
      if (element) {
        element.setAttribute("aria-invalid", "false");
        element.setAttribute("class", "form-check-input");
      }
      element = document.getElementById("termandcondcheck2");
      if (element) {
        element.setAttribute("aria-invalid", "false");
        element.setAttribute("class", "form-check-input");
      }
    },
    validateEmail: function(email) {
      var re = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return re.test(email);
    },
    applyCoupon: function() {
      var d = this.$data;
      if (d.coupon && d.coupon !== null && d.coupon !== "") {
        this.getInfo();
      }
    },
    getInfo: function() {
      var _this = this;

      if (globaldata.localizador) {
        _this.getDatafromLocator();
      }

      var d = this.$data;
      d.key.ratekey = this.$route.query.ratekey;
      d.key.datestart = this.$route.query.entrada;
      d.key.dateend = this.$route.query.salida;
      d.key.units = this.$route.query.units;
      d.key.adults = this.$route.query.adults;
      d.key.children = this.$route.query.children;
      d.key.name = this.$route.query.genericname;
      d.key.variantesel = this.$route.query.variantesel;
      d.key.variantename = this.$route.query.variantename;

      d.loader = true;
      if (d.key.suplsel) {
        d.suplementos = d.key.suplsel.split("~");
      }

      axios({
        method: "get",
        url:
          globaldata.baseurl +
          "/generic/pricedetails/" +
          this.$route.query.ratekey,
        params: {
          language: d.language,
          coupon: d.coupon
        }
      })
        .then(function(response) {
          d.loader = null;

          d.detalles = response.data;
        })
        .catch(function(error) {
          d.loader = null;
          console.log(error);
        });
    }
  },
  mounted() {
    this.getInfo();
      this.$data.fechasalidaformat = this.formatDatetoview(
      this.$route.query.salida
    );
    this.$data.fechaentradaformat = this.formatDatetoview(
      this.$route.query.entrada
    );
    this.getStorageData();
    helper.scrollToId("main-app");
  }
};
</script>

