<template>
  <div id="main-app"  class="transparent">
      <div id="steps" class="row ">
        <template v-if="showwhite">
          <div class="col-3">
            <button class="btn btn-primary rounded-circle"  style='cursor: default;min-width: 35px'>1</button><span class="step-text center" >  {{multilanguage.configureservice[language]}}</span>
          </div>
          <div class="col-3">
            <button class="btn btn-dark rounded-circle" disabled="disabled" style='cursor: default;min-width: 35px'> 2</button> <span class="step-text center" >  {{multilanguage.fillcheckbook[language]}}</span>
          </div>
        </template>
        <template v-else>
           <div class="col-3">
              <button class="btn btn-success rounded-circle" style='cursor: pointer;min-width: 35px'  @click="goback('formulario')">1</button>
            <a  style='cursor: pointer;' @click="goback('formulario')"><span class="step-text center" > {{multilanguage.selectdestination[language]}}</span></a>
          </div>
          <div class="col-3">
            <button class="btn btn-success rounded-circle" style='cursor: pointer;min-width: 35px' @click="goback('dispo')">2</button>
            <a style='cursor: pointer;' @click="goback('dispo')"><span class="step-text center" > {{multilanguage.selectservice[language]}}</span></a>
          </div>
          <div class="col-3">
            <button class="btn btn-primary rounded-circle"  style='cursor: default;min-width: 35px'>3</button><span class="step-text center" >  {{multilanguage.configureservice[language]}}</span>
          </div>
          <div class="col-3">
            <button class="btn btn-dark rounded-circle" disabled="disabled" style='cursor: default;min-width: 35px'>4</button><span class="step-text center" >  {{multilanguage.fillcheckbook[language]}}</span>
          </div>
        </template>
      </div>
    <div class="row">
      <div class="col-12 mt-5">
        <div class="othertitle">{{title}}</div>
      </div>
    </div>
    <form>
      <div class="row">
        <div class="col-sm-12 col-md-12 col-lg-6" >
          <Ficha :rate="this.$route.query.key"></Ficha>
        </div>
        <div class="col-sm-12 col-md-12 col-lg-6 text-center" >
          <div v-if="detalles">
            <template v-if="detalles.datesRangeDependant">
              <div class="calendar-title">{{multilanguage.changedates[language]}}</div>
              <div v-if="charged" >
                <v-date-picker  width="100%"
                                mode='range'
                                is-double-paned
                                v-model='selectedValueRange'
                                :is-linked = 'true'
                                :is-expanded='true'                                :available-dates='availabledates'
                                :disabled-dates= 'disableddates'
                                :is-inline='true'
                                :disabled-attribute='disableattr'
                                :drag-attribute='dragattr'
                                :select-attribute='selectattr'
                                :theme-styles='themeStyles'
                                tint-color="#FFE259"
                                show-caps>
                </v-date-picker>
                <input id="checkindates" class="validator" style="display:none;">
                <div class="invalid-feedback">
                  <span id="checkindates-msg" > </span>
                </div>
              </div>
              <div v-else class="s12">
                <template v-if="showwhite">
                  <a @click="goback('formulario') " style='cursor: pointer'><a @click="goback('formulario') " style='cursor: pointer'> {{multilanguage.nocalendar[language]}}</a></a>
                </template>
                <template v-else>
                  <a @click="goback('formulario')" style='cursor: pointer'> <a @click="goback('formulario') " style='cursor: pointer'> {{multilanguage.nocalendar[language]}}</a></a>
                </template>
              </div>
            </template>
            <template v-if="detalles.datesDependant">
              <div class="calendar-title text-center">{{multilanguage.changedates[language]}}</div>
              <div v-if="charged" class="s12">
                <v-date-picker width="100%"
                               mode='single'
                               is-double-paned
                               v-model='selectedValue'
                               :is-linked = 'true'
                               :is-expanded="true"
                               :available-dates='availabledates'
                               :disabled-dates= 'disableddates'
                               :is-inline='true'
                               :disabled-attribute='disableattr'
                               :drag-attribute='dragattr'
                               :select-attribute='selectattr'
                               :theme-styles='themeStyles'
                               tint-color="#FFE259"
                               show-caps>
                </v-date-picker>
                <input id="checkindates" class="validator" style="display:none;">
                <div class="invalid-feedback">
                  <span id="checkindates-msg" > </span>
                </div>
              </div>
              <div v-else class="s12">
                <template v-if="showwhite">
                  <a @click="goback('formulario') " style='cursor: pointer'> {{multilanguage.nocalendar[language]}}</a>
                </template>
                <template v-else>
                  <a @click="goback('formulario')" style='cursor: pointer'> {{multilanguage.nocalendar[language]}}</a>
                </template>
              </div>
            </template>
            </div>
          </div>
        </div>
      <div class="row">
        <template v-if="detalles">
          <div class="col-sm-12 col-md-12 col-lg-6">
              <div class="calendar-title text-center">{{multilanguage.configureyourservice[language]}}</div>

              <div class="row" >
                <div v-if="detalles.variantDependant" class="col-sm-12  form-group">
                  <label for="variante" class="active">{{multilanguage.variant[language]}}</label>
                  <select id="variante" class="form-control" v-model="variante">
                    <option v-for="(d,indexv) in detalles.variants" v-bind:value="d" :key="indexv">
                      <div><b>{{ d.name }}</b>
                        <template v-if="d.offer !== true">
                          <span class="secondary-content black-text"><b>{{ d.bestDeal.retailPrice.value}}</b> {{d.bestDeal.retailPrice.currencyIsoCode}}/ {{d.pricePer}}</span>
                        </template>
                        <template v-else>
                          <span class="badge red white-text"><b>{{ d.bestDeal.retailPrice.value}} </b>{{d.bestDeal.retailPrice.currencyIsoCode}}/ {{d.pricePer}} </span>
                        </template>
                      </div>
                      <div>{{d.description}}</div>
                    </option>
                  </select>
                  <div class="invalid-feedback">
                    <span id="varianteaux-msg" > </span>
                  </div>
                </div>
                <div v-if="detalles.adultsDependant" class="col-sm-12 col-md-6 col-lg-4 col-xl-4 form-group">
                  <label for="adultos"  class="active" >{{multilanguage.adults[language]}}</label>
                  <input type="number" class="form-control" data-error="wrong" id="adultos" v-model="key.adults" min="1" max="99"  required=""  >
                  <div class="invalid-feedback">
                    <span id="adultos-msg" > </span>
                  </div>
                </div>
                <div v-if="detalles.childrenDependant" class="col-sm-12 col-md-6 col-lg-4 col-xl-4 form-group">
                  <label for="ninos"  class="active" data-error="wrong">{{multilanguage.children[language]}}</label>
                  <input class="form-control" type="number" id="ninos" v-model="key.children" min="0" max="99"  aria-invalid="true" >
                  <div class="invalid-feedback">
                    <span id="ninos-msg" > </span>
                  </div>
                </div>
                <div v-if="detalles.unitsDependant" class="col-sm-12 col-md-6 col-lg-4 col-xl-4 form-group">
                  <label for="units"  class="active" data-error="wrong">{{multilanguage.units[language]}}</label>
                  <input class="form-control" type="number" id="units" v-model="key.units" min="1" max="99" aria-invalid="true" >
                  <div class="invalid-feedback">
                    <span id="units-msg" > </span>
                  </div>
                </div>
              </div>
            </div>
          <div class="col-sm-12 col-md-12 col-lg-6">
            <div class="calendar-title text-center">{{multilanguage.prices[language]}}</div>
            <div class="row" v-if="total">
              <div class="col-12 ">
                <table class="table mt-4 mb-4">
                  <thead>
                  <tr>
                    <th>{{multilanguage.description[language]}}</th>
                    <th class="text-right">{{multilanguage.value[language]}}</th>
                  </tr>
                  </thead>
                  <tbody >
                  <tr v-if=" total.value" style="margin-top:30px;">
                    <td class="pt-3">{{multilanguage.total[language]}}</td>
                    <td class="text-right pt-3"> {{ total.value.value }} {{
                      total.value.currencyIsoCode}}
                    </td>
                  </tr >
                  </tbody>
                </table>
                <hr class="pt-3">
              </div>
              <div class="col-12 text-center  button-container" v-if="total.available">
                <!-- <i class="material-icons green small circle">done</i><br/>{{multilanguage.available[language]}}-->
                <button type="button" class="btn btn-primary" v-on:click="book">{{multilanguage.goon[language]}}</button>
              </div>
              <div class="col-12 text-center  button-container" v-else>
                <button type="button" class="btn btn-primary" disabled>{{multilanguage.noavailable[language]}}</button>
              </div>
            </div>
          </div>
          </template>
        </div>
    </form>
  </div>
</template>

<script>

import axios from 'axios'
import helper from '@/script/helper';
import Ficha from '@/components/commons/Ficha';

var globallanguage = 'es'
if (globaldata.language != null && globaldata.language != '') {
  globallanguage = globaldata.language
}
const langfile = require('../../assets/lang/content.json')

var iswhitebrand = false
/*  if (globaldata.whitebrandexcursion !== null && globaldata.whitebrandexcursion !== '') {
    iswhitebrand = true
  } */

var today = new Date()
var tomorrow = new Date()
tomorrow.setDate(tomorrow.getDate() + 1)

export default {
  name: 'Rates',
  data: function () {
    return {
      showwhite: iswhitebrand,
      title: null,
      disponibilidad: null,
      availabledates: [],
      variante: null,
      variantes: [],
      pageloaded:false,
      disableddates: null,
      maprender: false,
      detalles: null,
      selectionMode: 'single',
      selectedValue: new Date(),
      selectedValueRange: {
        start: today,
        end: tomorrow
      },
      charged: null,
      key: {
        key: null,
        adults: 1,
        children: 0,
        units: 0,
        datestart: 0,
        dateend: 0,
        variantename: null
      },
      avdates: [],
      themeStyles: {
         wrapper: {
          background: '#fff',
          border: 'solid rgba(0, 0, 0, 0.2) 1px',
          borderRadius: '5px',
          padding: "10px"
        },
         headerTitle: {
          padding: "15px 0px",
          fontSize: "26px"
        },
        weekdays:{
          fontSize: "17px"
        },
        dayCellNotInMonth: {
          opacity: 0.7
        },
        dayCell: {
          background: '#00C064',
          color: '#fafafa',
          padding: '0px',
          margin: '0px'
        },
        dayContent: {
          margin: '0px',
          fontSize: "17px"
        }
      },
      disableattr:
          {
            key: 'disabled',
            highlight: {
              backgroundColor: '#F84744',
              opacity: 1,
              animated: false

            },
            contentStyle: {
              color: 'white'
            }
          },
      enableattr: [
        {
          key: 'available',
          highlight: {
            backgroundColor: '#4caf50',
            opacity: 1,
            animated: false
          },
          contentStyle: {
            color: 'white'
          }
        }],
      dragattr:
          {
            key: 'drag',
            highlight: {
              backgroundColor: '#ffc107',
              opacity: 1,
              animated: false
            },
            contentStyle: {
              color: 'black'
            }
          },
      selectattr:
          {
            key: 'select',
            highlight: {
              backgroundColor: '#ffb300',
              opacity: 1,
              animated: false
            },
            contentStyle: {
              color: 'black'
            }
          },
      total: null,
      loader: null,
      language: globaldata.language,
      multilanguage: langfile.multilanguage,
    }
  },
  watch: {
    variante: function (val) {
      if(this.$data.variante){
        this.$data.key.variantesel = this.$data.variante.key
        this.$data.key.variantename = this.$data.variante.name
        this.valorar()
      }
    },
    'key.children': function (val) {
      this.valorar()
    },
    'key.adults': function (val) {
      this.valorar()
    },
    'key.units': function (val) {
      this.valorar()
    },
    selectedValue: function (val) {

      this.$data.key.datestart = this.formatDate(val).replace(/-/g, '')
      this.$data.key.dateend = 0
      this.valorar()
    },
    selectedValueRange: function (val) {

      this.$data.key.datestart = this.formatDate(val.start).replace(/-/g, '')
      this.$data.key.dateend = this.formatDate(val.end).replace(/-/g, '')
      this.valorar()
    }
  },
components:{
    'Ficha': Ficha
    },
  methods: {
    valorar: function () {
      var d = this.$data

      if (this.$route.query.key) {
          axios({
            method: 'get',
            url: globaldata.baseurl + '/generic/check/' + this.$route.query.key,
            params: {
              language: d.language,
              adults: d.key.adults,
              children: d.key.children,
              start: d.key.datestart,
              end: d.key.dateend,
              units: d.key.units,
              variant: d.key.variantesel
            }
          })
            .then(function (response) {
              d.total = response.data
              d.key.ratekey = d.total.key
            })
            .catch(function (error) {
              console.log(error)
            })
      }
    },
    goback (page) {
      switch (page) {
        case 'formulario':
          this.$router.push({ name: 'main'})
          break
        case 'dispo':
          this.$router.push({
            name: 'otros_dispo',
            query: {
              destino: this.$route.query.destino
            }
          })
          break
        default:
      }
    },
    book: function (e) {
      e.preventDefault()
      if (this.checkForm()) {

        this.$router.push({
          name: 'otros_detalle',
          query: {
            ratekey: this.$data.key.ratekey,
            //destinoname: this.$route.query.destinoname,
            destino: this.$route.query.destino,
            genericname: this.$data.title,
            key: this.$route.query.key,
            variantesel: this.$data.key.variantesel,
            variantename: this.$data.key.variantename,
            entrada: this.$data.key.datestart ? this.$data.key.datestart.toString().substr(0, 4) + '-' + this.$data.key.datestart.toString().substr(4, 2) + '-' + this.$data.key.datestart.toString().substr(6, 2): null,
            salida: this.$data.key.dateend ? this.$data.key.dateend.toString().substr(0, 4) + '-' + this.$data.key.dateend.toString().substr(4, 2) + '-' + this.$data.key.dateend.toString().substr(6, 2): null,
            adults: this.$data.key.adults,
            children: this.$data.key.children,
            units: this.$data.key.units
          }
        })
      }
    },
    checkForm: function (e) {
      var d = this.$data
      var existsErrors = false
      this.cleanErrors()
      if (d.detalles.dateDependant && !d.key.datestart) {
        this.setValidationError('checkindates', 'checkindates-msg', this.multilanguage.errordatesempty[this.language])
        existsErrors = true
      }
      if (this.key.variantesel === null || this.key.variantesel === '') {
        this.setValidationError('variante', 'varianteaux-msg', this.multilanguage.errorvariantempty[this.language])
        existsErrors = true
      }
      if (d.detalles.datesRangeDependant && (!d.key.datestart || !d.key.dateend)) {
        this.setValidationError('checkindates', 'checkindates-msg', this.multilanguage.errordatesempty[this.language])
        existsErrors = true
      }
      if (d.detalles.unitsDependant && (!d.key.units || !d.key.units < 0)) {
        this.setValidationError('units', 'units-msg', this.multilanguage.errornounits[this.language])
        existsErrors = true
      }
      if (d.detalles.adultsDependant && !d.key.adults) {
        this.setValidationError('adultos', 'adultos-msg', this.multilanguage.errornopersonas[this.language])
        existsErrors = true
      }
      if (existsErrors) {
        return false
      } else {
        return true
      }
    },
    createDate: function (str) {
      var stringdate = str.toString()
      var year = stringdate.substr(0, 4)
      var month = stringdate.substr(4, 2)
      var day = stringdate.substr(6, 2)
      var formatdate = new Date(year, month - 1, day)

      return formatdate
    },
    formatDate: function (date) {
      var d = new Date(date)
      var month = '' + (d.getMonth() + 1)
      var day = '' + d.getDate()
      var year = d.getFullYear()

      if (month.length < 2) month = '0' + month
      if (day.length < 2) day = '0' + day

      return [year, month, day].join('-')
    },
    cleanErrors: function () {
      var d = this.$data

      var element

      if (d.detalles.variantDependant) {
        element = document.getElementById('variante')
        element.setAttribute('class', 'form-control bg-white')
      }
      if (d.detalles.adultsDependant) {
        element = document.getElementById('adultos')
        element.setAttribute('class', 'form-control bg-white')
      }
      if (d.detalles.childrenDependant) {
        element = document.getElementById('ninos')
        element.setAttribute('class', 'form-control bg-white')
      }
      if (d.detalles.unitsDependant) {
        element = document.getElementById('units')
        element.setAttribute('class', 'form-control bg-white')
      }
      if (d.detalles.datesDependant || d.detalles.datesRangeDependant) {
        element = document.getElementById('checkindates')
        element.setAttribute('class', 'form-control bg-white')
      }
    },
    setValidationError (field, msgField, msg) {

      var d = this.$data

      var element = document.getElementById(field)
      element.classList.add('is-invalid')
      element.classList.add('form-control')
      element.focus()

      if (msgField !== null && msgField !== ' ') {
        var elementmsg = document.getElementById(msgField)
        elementmsg.innerHTML = msg
      }
    },
    getrates: function () {
      var d = this.$data
      var _this = this
      axios({
        method: 'get',
        url: globaldata.baseurl + '/generic/rates/' + this.$route.query.key,
        params: {
          language: d.language
        }
      })
        .then(function (response) {
          d.detalles = response.data

          if (d.detalles.variantDependant) {
            d.variantes = d.detalles.variants
            if(d.variantes)
            {
              d.variante = d.variantes[0]
              //d.key.variantesel =d.variante
            }else{
              d.variante = ''
              d.key.variantesel = null
            }
          }

          if(d.detalles.datesRangeDependant || d.detalles.dateDependant){
            _this.getavailabledates()

            if (d.detalles.dateDependant) {
              d.key.datestart = _this.formatDate(d.selectedValue).replace(/-/g, '')
              d.key.dateend = 0
            }
            if (d.detalles.datesRangeDependant ) {
              d.key.datestart = _this.formatDate(d.selectedValueRange.start).replace(/-/g, '')
              d.key.dateend = _this.formatDate(d.selectedValueRange.end).replace(/-/g, '')
            }
          }
          else{
            d.key.datestart = null
            d.key.dateend = null
          }
        })
        .catch(function (error) {
          d.charged = false
          console.log(error)
        })
    },
    getavailabledates () {
      var d = this.$data
      var _this = this
      d.loader = true

      axios({
        method: 'get',
        url: globaldata.baseurl + '/cms/genericavailabilitycalendar',
        params: {
          product: this.$route.query.key
        }
      })
        .then(function (response) {
          var isFirst = true
          var startdate
          var enddate
          var lastdate
          var lastdateTrans

          d.loader = null
          d.disponibilidad = response.data

          if (d.disponibilidad) {
            for (var i = 0; i < d.disponibilidad.months.length; i++) {
              for (var j = 0; j < d.disponibilidad.months[i].weeks.length; j++) {
                for (var k = 0; k < d.disponibilidad.months[i].weeks[j].days.length; k++) {
                  if (d.disponibilidad.months[i].weeks[j].days[k].date !== null && d.disponibilidad.months[i].weeks[j].days[k].styleName === 'av') {
                    if (isFirst) {
                      startdate = d.disponibilidad.months[i].weeks[j].days[k].date
                      enddate = null
                      isFirst = false
                    } else {
                      var actualDate = new Date(d.disponibilidad.months[i].weeks[j].days[k].date)
                      actualDate.setDate(actualDate.getDate() - 1)
                      actualDate.setMilliseconds(0)
                      actualDate.setSeconds(0)
                      actualDate.setMinutes(0)
                      actualDate.setHours(0)
                      if (actualDate.getTime() !== lastdateTrans.getTime()) {
                        var ldate = lastdateTrans
                        ldate.setDate(ldate.getDate())
                        enddate = _this.formatDate(ldate)
                        // enddate = lastdate
                        d.availabledates.push({start: startdate, end: enddate})
                        startdate = d.disponibilidad.months[i].weeks[j].days[k].date
                      } else {

                      }
                    }
                    lastdate = d.disponibilidad.months[i].weeks[j].days[k].date
                    lastdateTrans = new Date(d.disponibilidad.months[i].weeks[j].days[k].date)
                    lastdateTrans.setMilliseconds(0)
                    lastdateTrans.setSeconds(0)
                    lastdateTrans.setMinutes(0)
                    lastdateTrans.setHours(0)
                  }
                }
              }
            }
            // if (ldate !== null && lastdate !== null ){
            var ldate = new Date(lastdate)
            ldate.setDate(ldate.getDate() + 1)
            enddate = _this.formatDate(ldate)

            if (startdate && enddate) {
              d.availabledates.push({start: startdate, end: enddate})
            }
            //  }
          }
          if (d.availabledates.length === 0) {
            d.availabledates = null
            d.disableddates = {start: null, end: null}
          }
          d.charged = true
        })
        .catch(function (error) {
          d.charged = false
          d.loader = null
          console.log(error)
        })
    },
    formatDate: function (date) {
      var d = new Date(date)
      var month = '' + (d.getMonth() + 1)
      var day = '' + d.getDate()
      var year = d.getFullYear()

      if (month.length < 2) month = '0' + month
      if (day.length < 2) day = '0' + day

      return [year, month, day].join('-')
    },
    formatDateInt: function (date) {
      var d = new Date(date)
      var month = '' + (d.getMonth() + 1)
      var day = '' + d.getDate()
      var year = d.getFullYear()

      if (month.length < 2) month = '0' + month
      if (day.length < 2) day = '0' + day

      return [year, month, day].join('')
    }
  },
  mounted () {

    var d = this.$data
    var _this = this

    if(this.$route.query.entrada){
      d.selectedValue =  this.$route.query.entrada
      d.selectedValueRange.start =  this.$route.query.entrada
    }
    if(this.$route.query.salida){
      d.selectedValueRange.end =  this.$route.query.salida
    }

    d.key.adults = this.$route.query.adults
    d.key.children = this.$route.query.children
    d.key.units = this.$route.query.units

    this.getrates()
    this.valorar()
    helper.scrollToId("main-app");
  },
}
</script>

<style scoped>
  .calendar-title {
    line-height: 48px;
    height: 48px;
    font-size: 2em
  }
  .success-step {
    background-color: #4caf50 ;
    color:white ;
  }
  .success-step:hover{
    background-color: #4caf50;
    color:white ;
    opacity: 0.8 ;
