<script>

import axios from 'axios'
import noUiSlider from 'nouislider'
import wNumb from 'wnumb'
import 'nouislider/distribute/nouislider.css'
import helper from '@/script/helper';

var slider
var globallanguage = 'es'
if (globaldata.language != null && globaldata.language != '') {
  globallanguage = globaldata.language
}
var viewtypes = true
var globaltype = null

if (globaldata.servicetype !== null && globaldata.servicetype !== '') {
  globaltype = globaldata.servicetype
  viewtypes = false
}
const langfile = require('../../assets/lang/content.json')

export default {
  name: 'OtrosDispo',
  data: function () {
    return {
      dispo: null,
      loader: null,
      services: [],
      serviceArray: [],
      pricesrange: null,
      hidefilter: true,
      language: globaldata.language,
      multilanguage: langfile.multilanguage,
      servicetype: globaltype,
      whitebrandservicetype: viewtypes,
      maxPrice: null,
      minPrice: null,
      labels: []

    }
  },
  watch: {
    labels: function () {
      this.fiterresults()
    }
  },
  methods: {

    abrirdetalle (key, name) {
      this.deletetooltips()
      this.$router.push({
        name: 'otros_rates',
        query:
            {
              key: key,
              name: name,
              destino: this.$route.query.destino
            }
      })
    },
    goback (page) {
      this.$router.push({name: 'main'})
    },
    resetfilter () {
      var _this = this
      var d = this.$data

      if (d.maxPrice >= 0 && d.minPrice >= 0 && d.maxPrice !== d.minPrice) {
        slider.noUiSlider.reset()
      }
      _this.getAvailability()
    },
    deletetooltips () {
      var element = document.getElementsByClassName('material-tooltip'), index
      for (index = element.length - 1; index >= 0; index--) {
        element[index].parentNode.removeChild(element[index])
      }
    },

    fiterresults () {
      var d = this.$data
      var _this = this
      d.loader = true
      var prices = [null, null]
      if (d.maxPrice >= 0 && d.minPrice >= 0 && d.maxPrice !== d.minPrice) {
        prices = slider.noUiSlider.get()
      }
      var selectedLables = ''
      for (var i = 0; i < d.labels.length; i++) {
        if (d.labels[i]) {
          selectedLables = selectedLables + d.dispo.labels[i].id + ','
        }
      }

      axios({
        method: 'get',
        url: globaldata.baseurl + '/generic/filter',
        params: {
          destination: this.$route.query.destino,
          language: d.language,
          minprice: prices[0],
          maxprice: prices[1],
          labels: selectedLables === '' ? null : selectedLables

        }
      })
        .then(function (response) {
          d.loader = null
          d.dispo = response.data


        })
        .catch(function (error) {
          d.loader = null
          console.log(error)
        })
    },

    getDatafromLocator: function () {
      var d = this.$data
      var _this = this

      axios({
        method: 'get',
        url: globaldata.baseurl + '/commons/fromlocator/' + globaldata.localizador,
        params: {}
      })
        .then(function (response) {
          _this.$route.query.iddestino = response.data.destination
          _this.$route.query.destino = response.data.destinationName
          _this.$route.query.entrada = response.data.checkin.toString().substr(0, 4) + '-' + response.data.checkin.toString().substr(4, 2) + '-' + response.data.checkin.toString().substr(6, 2)
          _this.$route.query.salida = response.data.checkout.toString().substr(0, 4) + '-' + response.data.checkout.toString().substr(4, 2) + '-' + response.data.checkout.toString().substr(6, 2)
          _this.$route.query.ocupacion = response.data.occupation
          d.selectedValue[0].start = response.data.checkin.toString().substr(0, 4) + '-' + response.data.checkin.toString().substr(4, 2) + '-' + response.data.checkin.toString().substr(6, 2)
          d.selectedValue[0].end = response.data.checkout.toString().substr(0, 4) + '-' + response.data.checkout.toString().substr(4, 2) + '-' + response.data.checkout.toString().substr(6, 2)
          _this.getAvailability()
        })
        .catch(function (error) {
          d.loader = null
          console.log(error)
        })
    },
    getAvailability () {
      var d = this.$data
      var _this = this

      axios({
        method: 'get',
        url: globaldata.baseurl + '/generic/available',
        params: {
          destination: this.$route.query.destino,
          language: d.language

        }
      })
        .then(function (response) {
          d.dispo = response.data

          for (var i = 0; i < d.dispo.labels.length; i++) {
            d.labels[i] = false
          }

          d.minPrice = d.dispo.minPrice
          d.maxPrice = d.dispo.maxPrice

          setTimeout(function () {
            if (d.maxPrice >= 0 && d.minPrice >= 0 && d.maxPrice !== d.minPrice) {
              var step = 10

              if (((d.maxPrice - d.minPrice) < 50) && ((d.maxPrice - d.minPrice) >= 20)) {
                step = 5
              } else if (d.maxPrice !== d.minPrice < 20) {
                step = 1
              }

              slider = document.getElementById('test-slider')

              if (!slider.noUiSlider) {
                noUiSlider.create(slider, {
                  start: [d.minPrice === 0 ? 0 : d.minPrice - 1,
                    d.maxPrice + 1],
                  connect: true,
                  step: step,
                  tooltips: [true, true],
                  pips: {
                    mode: 'count',
                    values: 6,
                    density: 4
                  },
                  orientation: 'horizontal', // 'horizontal' or 'vertical'
                  range: {
                    'min': d.minPrice === 0 ? 0 : d.minPrice - 1,
                    'max': d.maxPrice === 0 ? 0 : d.maxPrice + 1
                  },
                  format: wNumb({
                    decimals: 0
                  })
                })
                slider.noUiSlider.on('change', function (values, handle) {
                  _this.fiterresults()
                })
              }
            }
          })
        })
        .catch(function (error) {
          console.log(error)
        })
    }
  },
  mounted () {
    var d = this.$data
    var _this = this

    /* if (globaldata.localizador && !this.$route.query.iddestino) {

         _this.getDatafromLocator()
       } else { */
    _this.getAvailability()
    helper.scrollToId("main-app");
    /* } */
  }
}
</script>

